body {
  font-family: arial;
  font-size: 17px;
  line-height: 1.5;
  /* background-color: #F5F8FA; */
}

h1 {
  text-align: center;
  font-size: 250%;
  font-weight: 900;
  line-height: 1.2;
}

.subtitle {
  text-align: center;
  text-transform:uppercase;
}
.dicsl{text-align: center;}
.filler {
  padding-left: 30vw;
  padding-right: 30vw;
}
.title {
  padding-left: 15vw;
  padding-right: 15vw;
  text-align: center;
}
/*     .title h1{
  font-weight:300;
  font-size:64px; 
} */
#scrolly-overlay .scrolly {
  max-width: 75rem;
  margin: 3rem auto;
  background-color: none;
  padding: 1rem;
}
#scrolly-overlay .scrolly article {
  padding: 0;
  max-width: 40rem;
  margin: 0 auto;
  position: relative;
}
#scrolly-overlay .scrolly article .step {
  min-height: 80vh;
  margin-bottom: 1rem;
}
#scrolly-overlay .scrolly article .step:last-of-type {
  margin-bottom: 0;
}

/*     .step div{display:inline-block} */

#scrolly-overlay .scrolly article .step.is-active p {
  opacity: 0.6;
}
#scrolly-overlay .scrolly article .step p {
  margin: 0;
  padding: 1rem;
  text-align: center;
  font-weight: 400;
  background-color: #fff;
  transition: background-color 250ms ease-in-out;
  color: #000;
  border: 2px solid #657786;
  opacity: 0.9;
}

#scrolly-overlay .scrolly figure.sticky {
  position: sticky;
  width: 100%;
  height: 65vh;
  background: none;
  margin: 0;
  top: 10vh;
  left: 0;
}

article {
  pointer-events: none;
}

/*     svg{   background: #eee;} */

.tick,.plot-f4e00de1f5fdf8 > g:nth-child(2) > text:nth-child(13) {
  font-size: 14px;
  /* color:#657786 */
}

@media (max-width: 667px) {
  .tick,.plot-f4e00de1f5fdf8 > g:nth-child(2) > text:nth-child(13) {
    font-size: 22px;
  }

  #chart > svg > g:nth-child(11),#chart > svg > g:nth-child(10){
    font-size: 22px;
  }

  #scrolly-overlay .scrolly article .step p {
    line-height: 1.2;
    font-size: 14px;
  }
  #scrolly-overlay .scrolly figure.sticky {
    height: 30vh;
  }

  #bottom {
    padding-top: 10px;
  }

  .filler {
    padding-left: 2vw;
    padding-right: 2vw;
  }
  .title {
    padding-left: 2vw;
    padding-right: 2vw;
  }
  /*        .title h1{
  font-size:34px; 
} */
}

